import newsNavBar from "../../components/news/news-nav-bar.vue";
import commonList from "../../components/common/common-list.vue";
import indexList from "../../components/index/index-list.vue";
import loadMore from "../../components/common/load-more.vue";
import topicNav from "../../components/news/topic-nav.vue";
import topicList from "../../components/news/topic-list.vue";
import {
	mapState
} from 'vuex'
export default {
	components: {
		newsNavBar,
		commonList,
		loadMore,
		topicNav,
		topicList,
		indexList
	},
	computed: {
		...mapState(['userInfo'])
	},
	data() {
		return {
			swiperheight: 500,
			tabIndex: 0,
			tabBars: [{
					name: "关注",
					id: "guanzhu"
				}
				// ,
				// {
				// 	name: "话题",
				// 	id: "topic"
				// }
			],
			guanzhu: {
				loadtext: "",
				page: 1,
				list: []
			},
			topic: {
				swiper: [{
						src: "../../static/demo/banner2.jpg"
					},
					{
						src: "../../static/demo/banner2.jpg"
					},
					{
						src: "../../static/demo/banner2.jpg"
					},
				],
				nav: [{
						name: "最新"
					},
					{
						name: "游戏"
					},
					{
						name: "打卡"
					},
					{
						name: "情感"
					},
					{
						name: "故事"
					},
					{
						name: "喜爱"
					},
				],
				list: [{
						titlepic: "../../static/demo/topicpic/13.jpeg",
						title: "话题名称",
						desc: "我是话题描述",
						totalnum: 50,
						todaynum: 10
					},
					{
						titlepic: "../../static/demo/topicpic/13.jpeg",
						title: "话题名称",
						desc: "我是话题描述",
						totalnum: 50,
						todaynum: 10
					},
					{
						titlepic: "../../static/demo/topicpic/13.jpeg",
						title: "话题名称",
						desc: "我是话题描述",
						totalnum: 50,
						todaynum: 10
					},
					{
						titlepic: "../../static/demo/topicpic/13.jpeg",
						title: "话题名称",
						desc: "我是话题描述",
						totalnum: 50,
						todaynum: 10
					},
					{
						titlepic: "../../static/demo/topicpic/13.jpeg",
						title: "话题名称",
						desc: "我是话题描述",
						totalnum: 50,
						todaynum: 10
					},
					{
						titlepic: "../../static/demo/topicpic/13.jpeg",
						title: "话题名称",
						desc: "我是话题描述",
						totalnum: 50,
						todaynum: 10
					}
				]
			}
		};
	},
	onShow() {
		if(this.userInfo.id &&this.guanzhu.list.length==0){
			this.requestData()
			this.guanzhu.loadtext = ""
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: (res) => {
				let height = res.windowHeight - uni.upx2px(100)
				this.swiperheight = height;
			}
		});
		if(this.userInfo.id){
			this.requestData()
		}else{
			this.guanzhu.loadtext = "你还未登录呢!"
		}
	},
	methods: {
		async requestData(GoPage) {
			let currentPage = GoPage || this.guanzhu.page;
			let data;
			try {
				data = await this.$http.get(`/topic/page?page=${currentPage}&rows=10`)
			} catch (e) {
				console.log(e)
				return
			}
			let {
				page,
				items
			} = data
			if (items && items.length === 0) {
				this.guanzhu.page = page
				this.guanzhu.loadtext = "没有更多数据了";
				return
			}
			items.forEach((item) => {
				if (item.images != '') {
					item.images = item.images.split(",");
				} else {
					item.images = []
				}
			})
			this.guanzhu.page = page
			this.guanzhu.list = this.guanzhu.list.concat(items)
			if (items && items.length < 10) {
				this.guanzhu.loadtext = "没有更多数据了";
			}
		},
		async likeOrTread(data) {
			await this.$http.post('topic/active', data);
		},
		// 点击切换
		changeTab(index) {
			this.tabIndex = index;
		},
		// 滑动事件
		tabChange(e) {
			this.tabIndex = e.detail.current;
		},
		// 上拉加载
		loadmore() {
			console.log(this.guanzhu)
			if (this.guanzhu.loadtext != "") {
				return;
			}
			// 修改状态
			this.guanzhu.loadtext = "加载中...";
			this.requestData(this.guanzhu.page+1)
			// 获取数据
			// this.guanzhu.loadtext="没有更多数据了";
		}

	}
}
